@media screen and (min-width: 500px) {
  .container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .Rank {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    margin-top: 80px;
    overflow:visible;
    width: 850px;
  }

  .filters {
    grid-column: span 3;
    display: flex;
    width: 100%;
    justify-content: flex-start;
    gap: 16px;
  }

  .MuiButton-root {
    display: none !important;
  }

  .filter-drawer {
    display: none;
  }

}

@media screen and (max-width: 500px) {

  :root {
    --screen-height: -webkit-fill-available
  } 
  
  .Rank {
    display: grid;
    height: calc(100% - 16px);
    overflow: hidden;
    grid-template-rows: 30px 1fr;
    grid-template-columns: 400px;
    row-gap: 16px;
    justify-content: center;
    align-items: center;
    justify-items: center;
    padding-top: 16px;
  }

  .filters {
    grid-column: span 1;
    justify-self: end;
    display: flex;
    justify-content: flex-end;
    gap: 16px;
  }

  #artist-filter {
    display: none;
  }

  #year-filter {
    display: none;
  }

  #ranking-filter {
    display: none;
  }

  .container {
    overflow: hidden;
    box-sizing: border-box;
    max-height: calc(100% - 16px);
  }

  .MuiButton-root {
    margin-right: 24px !important;
  }

  #rank-page-container {
    height: -webkit-fill-available;
    max-height: 97vh;
  }

  .filter-drawer {
    position: fixed;
    top: 100%;
    bottom: 0;
    width: 100%;
    height: -webkit-fill-available;
    background-color: #211D27;
    border-radius: 20px 20px 0 0;
    transition: all 0.5s ease-out;
  }

  .filter-drawer-content {
    padding: 40px 24px 0px 24px;
  }

  .filter-drawer-header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
  }

  .filter-drawer-header-title {
    justify-self: center;
    margin-left: 40px;
    font-size: 20px;
    color: #CFCFCF;
    font-weight: 600;
  }

}
